.Container {
    font-family: 'Pirata One', cursive;
    width: 100vw;
    height: auto;
    padding: 10px;
    padding-bottom: 25px;
    background: linear-gradient(180deg, #FFFFFF 0%, #000000 100%);

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items:center;

    position: relative;
}

.Title {
    font-family: 'DuvallSmallCaps', sans-serif;
    text-shadow: 3px 3px #2e2400 !important;
    font-size: 2.5rem;
}

.Subtitle {
    font-size: 1em;
    margin-top: "20px";
    margin-left: 5px;
    text-shadow: 2px 2px #000000;
}


.Link {
    font-size: 1.5rem;
    margin-top: "20px";
    text-shadow: 1px 1px white;
}

@media screen and (max-width: 700px) {
    .Container {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .Title {
        color: black;
        text-shadow: 3px 3px white !important;
        font-weight: bolder;
        margin-bottom: 20px;
    }

    .Subtitle {
        margin-top: -42px;
        color: rgb(214, 181, 12);
        font-size: large;
        text-shadow: 1px 1px #000000;
    }
    
    .Link {
        margin-top: -30px;
        font-size: xx-large;
        color: grey;
        text-shadow: 2px 2px #000000;
    }
  }
